// import Binding from './Binding'
// import Compile from './Compile'
// import { createComputed, createMounted } from './lifecycle'

class myVue3 {
    // åˆå§‹åŒ–
    constructor(options = {}) {
        // ä¿ç•™é…ç½®é¡¹
        this.$options = options;
        // èŽ·å–æ•°æ®
        let data = this._data = this.$options.data;
        // åˆå§‹åŒ–ç›‘å¬æ•°ç»„
        this.binding = new Binding();
        // è®¢é˜…æ•°æ®
        this._data = this.observe.call(this, data);
        // åˆå§‹åŒ–è®¡ç®—å±žæ€§
        // createComputed.call(this);
        // ç¼–è¯‘ DOM ä¸­çš„ Vue è¯­æ³•å…ƒç´ 
        new Compile(options.el, this)
    }
    // åŒå‘ç»‘å®šè®¢é˜…æ•°æ®
    observe(data) {
        const that = this;
        // å¦‚æžœä¸å­˜åœ¨æˆ–ä¸æ˜¯å¯¹è±¡åˆ™è¿”å›žåŽŸæ•°æ®
        if (!data || typeof data !== 'object') return data;
        // å¦åˆ™åŠ å…¥è®¢é˜…
        return new Proxy(data, {
            get: (target, prop) => {
                return Reflect.get(target, prop);
            },
            set: (target, prop, value) => {
                // æ›´æ–°å¯¹åº”æ•°æ®
                let result = Reflect.set(target, prop, value);
                // å‘å¸ƒè®¢é˜…
                that.binding[prop].map(item => {
                    item.update();
                });
                return result;
            }
        })
    }
}

